.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1200px;
  min-width: 100vw;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 1px;
}
body {
  background: rgb(201, 169, 235);
  // background: linear-gradient(45deg, rgb(207, 191, 154), rgb(26, 11, 90));
  font-family: "Lato", sans-serif;
  font-size: 24px;
}
ul,
li,
section {
  margin: 0;
}
